import React, { Component } from 'react';
import { Button, Card, CardBody, CardHeader, Col, Container, Jumbotron, Row } from 'reactstrap';

class AllReports extends Component {

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col>
            <Card>
              <CardHeader>
                <strong>Subcontract Reports</strong>
                <div className="card-header-actions">
                  <a href="/data-dashboard/#/reports/subcontract-reports" rel="noreferrer noopener" target="_blank" className="card-header-action">
                    <small className="text-muted">new</small>
                  </a>
                </div>
              </CardHeader>
              <CardBody>
                  <p className="lead">This report contains all budget, scope change, forecast, invoiced, spent, expended and committed dollars for your project</p>
                  <hr className="my-2" />
                  <p>Cost, Invoice, Expended Reports</p>
                  <p className="lead">
                    <a href="/data-dashboard/#/reports/subcontract-reports"><Button color="primary">Access</Button></a>
                  </p>

              </CardBody>
            </Card>
          </Col>
          <Col>
            <Card>
              <CardHeader>
                <strong> Other Demo Module 1</strong>
                <div className="card-header-actions">
                  <a href="#" rel="noreferrer noopener" target="_blank" className="card-header-action">
                    <small className="text-muted">demo</small>
                  </a>
                </div>
              </CardHeader>
              <CardBody>
  
                  <p className="lead">User friendly description for our Demo module 1</p>
                  <hr className="my-2" />
                  <p>Optional/Secondary description</p>
                  <p className="lead">
                    <Button color="disabled">Access</Button>
                  </p>

              </CardBody>
            </Card>
          </Col>
        </Row>
          <Row>
          <Col>
            <Card>
              <CardHeader>
                <strong> Other Demo Module 2</strong>
                <div className="card-header-actions">
                  <a href="#" rel="noreferrer noopener" target="_blank" className="card-header-action">
                    <small className="text-muted">demo</small>
                  </a>
                </div>
              </CardHeader>
              <CardBody>

                  <p className="lead">User friendly description for our Demo Module 2</p>
                  <hr className="my-2" />
                  <p>Optional/Secondary description</p>
                  <p className="lead">
                    <Button color="disabled">Access</Button>
                  </p>

              </CardBody>
            </Card>
          </Col>
          <Col>
            <Card>
              <CardHeader>
                <strong> Other Demo Module 3</strong>
                <div className="card-header-actions">
                  <a href="#" rel="noreferrer noopener" target="_blank" className="card-header-action">
                    <small className="text-muted">demo</small>
                  </a>
                </div>
              </CardHeader>
              <CardBody>

                  <p className="lead">User friendly description for our Demo module 3</p>
                  <hr className="my-2" />
                  <p>Optional/Secondary description</p>
                  <p className="lead">
                    <Button color="disabled">Access</Button>
                  </p>

              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default AllReports;



